<template>
    <div class="m-container">
        <Header></Header>
        <div class="block">
            <el-timeline>
                <el-timeline-item v-bind:timestamp="blog.created" placement="top" v-for="(blog,index) in blogs" :key="index">
                    <el-card class="card">
                        <h4>
                            <router-link class="alink" :to="{name: 'BlogDetail', params: {blogId: blog.id}}">{{ blog.title }}
                            </router-link>
                        </h4>
                        <p class="desc">{{ blog.description }}</p>
                    </el-card>
                </el-timeline-item>
            </el-timeline>

        </div>
        <el-pagination class="page"
                       background
                       layout="prev, pager, next"
                       :current-page=currentPage
                       :page-size=pageSize
                       @current-change=page
                       :total="total">
        </el-pagination>
        <Footer></Footer>
    </div>
</template>
<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";

export default {
    name: "Blogs",
    components: {Header,Footer},
    data() {
        return {
            blogs: {},
            currentPage: 1,
            total: 0,
            pageSize: 5
        }
    },
    methods: {
        page(currentPage) {
            const _this = this
            _this.$axios.get('/blogs?currentPage=' + currentPage).then((res) => {
                //输出所有的记录信息
                // console.log(res.data.data.records)
                _this.blogs = res.data.data.records
                _this.currentPage = res.data.data.current
                _this.total = res.data.data.total
                _this.pageSize = res.data.data.size
            })
        }
    },
    mounted() {
        this.page(1);
    }
}
</script>
<style scoped>
.block {
    margin: 20px;
}

.page {
    margin: 0 auto;
    text-align: center;
}

.card:hover{
    box-shadow: 0 2px 4px 0px rgb(0 0 0 / 10%), 0 4px 8px 0px rgb(0 0 0 / 10%), 0 8px 16px 0px rgb(0 0 0 / 10%), 0 16px 32px 0px rgb(0 0 0 / 10%);
}

.alink{
    text-decoration: none;
    font-size: 18px;
    color: #4c4948;
}
.alink:hover{
    color: #8e8cd8;
}
.desc{
    line-height: 24px;
    font-size: 15px;
}

</style>
